// 
import request from '../utils/request.js'

//  - 
export const loginAPI = (username, password) => {
  return request({
    url: '/api/login',
    method: 'POST',
    data: {
      username,
      password
    },
	headers: {
	  'content-type': 'application/x-www-form-urlencoded'
	}
  })
}

//  - 
export const getUserInfoAPI = (token) => {
  return request({
    url: '/my/userinfo',
	headers: {
	  'Authorization': token,
	}
  })
}